import React, { useCallback } from 'react';
import { v4 as uuid } from 'uuid';

import { InputDynamic } from '../common/InputDynamic';
import { FormField } from '../common/FormField';

import { TextareaProps } from './interfaces';

export function Textarea<T>({
  name,
  className,
  onChange,
  onChangeField,
  label,
  htmlFor = true,
  hasDirty,
  hasError,
  disabled,
  hint,
  error,
  ...rest
}: TextareaProps<T>) {
  const handleCallback = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;

    onChange?.(value);
    onChangeField?.(value, name as T);
  }, [onChange, onChangeField, name]);

  const forId = (htmlFor && label && uuid()) || '';

  return (
    <FormField
      className={className}
      label={label}
      disabled={disabled}
      forId={forId}
      hasDirty={hasDirty}
      hasError={hasError}
      hint={hint}
      error={error}
    >
      <InputDynamic
        {...rest}
        id={forId}
        disabled={disabled}
        hasDirty={hasDirty}
        hasError={hasError}
        tag="textarea"
        name={name as string | undefined}
        onChange={handleCallback}
      />
    </FormField>
  );
}
