
import React, { useCallback, ChangeEvent, useMemo } from 'react';

import { Input } from '../Input';

import { InputNumberProps } from './interfaces';

export function InputNumber<T>({
  name,
  value,
  maxCount,
  onChange,
  onChangeField,
  withMask,
  ...rest
}: InputNumberProps<T>) {
  const handleInput = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;

    let numberedValue = Number(value);

    if (value.trim()) {
      if (value.match(/[^\d]/g)) {
        const replacedValue = value.replace(/[^\d]/g, '');

        numberedValue = (replacedValue && Number(replacedValue)) || -1;
      }
    } else {
      numberedValue = -1;
    }

    if (!withMask && maxCount && numberedValue > maxCount) {
      numberedValue = Number(maxCount);
    }

    onChangeField?.(numberedValue, name as T);
    onChange?.(numberedValue);
  }, [maxCount, name, onChange, onChangeField, withMask]);

  const validValue = useMemo(() => {
    if (value && value > -1) {
      if (!withMask && maxCount) {
        return (Number(value) > maxCount && maxCount) || value;
      }
    }

    return value;
  }, [value, withMask, maxCount]);

  return (
    <Input
      {...rest}
      value={validValue}
      name={name}
      onInput={handleInput}
    />
  );
}
