export interface LargeProps {
  value?: string | undefined;
  children?: React.ReactNode;
  weight?: keyof typeof LargeClassNameEnum;
  className?: string | undefined;
  margin?: string;
  maxWidth?: number;
}

export enum LargeClassNameEnum {
  bold = 'uiKit-typography-large-bold',
  regular = 'uiKit-typography-large-regular',
  semiBold = 'uiKit-typography-large-bold',
}

export enum LargeWeightEnum {
  bold = 'bold',
  regular = 'regular',
  semiBold = 'bold',
}
