import React, { useState, useMemo, useCallback } from 'react';

import { PagingItem } from './PagingItem/intex';
import { PagingRow } from './PagingRow/index';
import { DotsItem } from './DotsItem/index';
import { PagingProps } from './interfaces';
import { DOTS_LIMIT, MAX_LIMIT } from './constants';

import './styles.css';

export const Paging = (props: PagingProps): JSX.Element => {
  const {
    maxPage, initialCurrentPage = 1, handleCallback, className = ''
  } = props;

  // State
  const [currentPagePagging, setCurrentPage] = useState<number>(initialCurrentPage);

  // Memo
  const memoClassName: string = useMemo(() => `uiKit-paging-wrapper ${className}`, [className]);

  // Callback
  const handleSelect = useCallback(
    (value: number) => () => {
      setCurrentPage(() => value);
      if (handleCallback) handleCallback(value);
    },
    [handleCallback],
  );

  const isActive = useCallback(
    (value: number): boolean => value === currentPagePagging,
    [currentPagePagging],
  );

  return (
    <div className={memoClassName}>
      <PagingItem value="1" active={isActive(1)} handleSelect={handleSelect(1)} />
      {currentPagePagging >= DOTS_LIMIT && maxPage > MAX_LIMIT && <DotsItem />}
      <PagingRow currentPage={currentPagePagging} maxPage={maxPage} handleSelect={handleSelect} />
      {currentPagePagging <= maxPage - DOTS_LIMIT && maxPage > MAX_LIMIT && <DotsItem />}
      <PagingItem
        value={String(maxPage)}
        active={isActive(maxPage)}
        handleSelect={handleSelect(maxPage)}
      />
    </div>
  );
};
