export interface NotificationProps {
  /**
   * Value label
   * */
  value: string;
  /**
   * Size notification
   * @default 'regular'
   */
  size?: keyof typeof NotificationSizeEnum;
  /**
   * Type notification
   * @default 'default'
   */
  type?: keyof typeof NotificationClassEnum;
  /**
   * Closable notification
   * @default true
   */
  closable?: boolean;
  /**
   * Your className
   */
  className?: string | undefined;
  /**
   * ID for targeting close
   */
  id: number;
  /**
   * closeCallback
   */
  closeCallback?: (id: number) => void;
}

export enum NotificationClassEnum {
  danger = 'uiKit-notification-danger',
  default = 'uiKit-notification-default',
  success = 'uiKit-notification-success',
  warning = 'uiKit-notification-warning',
}

export enum NotificationTypeEnum {
  danger = 'danger',
  default = 'default',
  success = 'success',
  warning = 'warning',
}

export enum NotificationSizeEnum {
  regular = 'regular',
  small = 'small',
}
