export interface TypographyProps {
  type?: keyof typeof TypographyType;
  weight?: keyof typeof TypographyFontWeight;
  value?: string | number;
  className?: string;
  margin?: string;
  ellipsis?: boolean;
  multilineEllipsis?: number;
  tag?: keyof typeof TypographyTag;
  font?: keyof typeof TypographyFont;
}

export enum TypographyType {
  body = 'body',
  h1 = 'h1',
  h2 = 'h2',
  h3 = 'h3',
  h4 = 'h4',
  large = 'large',
  small = 'small',
}

export enum TypographyFontWeight {
  bold = 'bold',
  regular = 'regular',
  semibold = 'semibold',
}

export enum TypographyTag {
  figcaption = 'figcaption',
  h1 = 'h1',
  h2 = 'h2',
  h3 = 'h3',
  h4 = 'h4',
  h5 = 'h5',
  h6 = 'h6',
  p = 'p',
  span = 'span'
}

export enum TypographyFont {
  officina = 'officina-serif',
  sourceSans = 'source-sans-pro'
}
