import React, { useMemo, useCallback } from 'react';

import { RadioProps } from './interfaces';

import './styles.css';

export const Radio = (props: RadioProps): JSX.Element => {
  const {
    isActive,
    hasError = false,
    disabled = false,
    onClick,
    className = '',
    name = '',
    value = '',
  } = props;

  // Memo
  const memoClassName: string = useMemo(
    () => `uiKit-Radio${(hasError && '_hasError') || ''} ${className}`,
    [hasError, className],
  );

  const handleCallback = useCallback(
    (e) => {
      if (onClick) onClick(e);
    },
    [onClick],
  );

  return (
    <input
      name={name}
      className={memoClassName}
      type="Radio"
      disabled={disabled}
      defaultChecked={isActive}
      onChange={handleCallback}
      value={value}
    />
  );
};
