import React, { useMemo } from 'react';

import { Typography } from '../../../Typography';

import { PagingItemProps } from './interfaces';

import './styles.css';

export const PagingItem = (props: PagingItemProps): JSX.Element => {
  const { value, active, handleSelect } = props;

  const memoClassName: string = useMemo(
    () => `uiKit-pagging-paggingItem ${(active && 'uiKit-pagging-paggingItem-active') || ''}`,
    [active],
  );

  return (
    <div
      tabIndex={0}
      role="button"
      onClick={handleSelect}
      onKeyDown={handleSelect}
      className={memoClassName}
    >
      <Typography value={value} />
    </div>
  );
};
