
import React, { useCallback, useState } from 'react';

import { InputDynamicType } from '../common/InputDynamic';
import { Input } from '../Input';

import { InputPasswordProps } from './interfaces';

export function InputPassword<T>({ ...rest }: InputPasswordProps<T>) {
  const [isShow, setIsShow] = useState(false);

  const handleIconClick = useCallback(() => {
    setIsShow((prev) => !prev);
  }, []);

  return (
    <Input
      {...rest}
      autoComplete="on"
      type={(isShow && InputDynamicType.text) || InputDynamicType.password}
      icon={(isShow && 'eyeCrossed') || 'eye'}
      onIconClick={handleIconClick}
    />
  );
}
