import React, { useMemo } from 'react';

import { SmallProps, SmallClassNameEnum, SmallWeightEnum } from './interfaces';

import '../../../../styles/prplMainFonts.css';
import './styles.css';

const Small = (props: SmallProps): JSX.Element => {
  const {
    value, weight = SmallWeightEnum.regular, children, className = '', margin, maxWidth
  } = props;

  const memoValue: string = useMemo(() => value || '', [value]);
  const memoClassName: string = useMemo(
    () => `${SmallClassNameEnum[weight] || SmallClassNameEnum.regular}${(maxWidth && '_eclipses') || ''} ${className}`,
    [weight, maxWidth, className],
  );

  const style = { margin: `${margin}`, maxWidth: (maxWidth && `${maxWidth}px`) || 'auto' };

  return (
    <p style={style} className={memoClassName}>
      {memoValue}
      {children}
    </p>
  );
};

export { Small };
