import React from 'react';

import { H1 } from './H1';
import { H2 } from './H2';
import { H3 } from './H3';
import { H4 } from './H4';
import { Large } from './Large';
import { Body } from './Body';
import { Small } from './Small';
import './styles.css';
import { TypeTypographyEnum, WeightTypographyEnum, TypographyProps } from './interfaces';

export const Typography = (props: TypographyProps): JSX.Element => {
  const {
    type = TypeTypographyEnum.body,
    weight = WeightTypographyEnum.regular,
    value = '',
    children,
    className: classProp = '',
    margin,
    maxWidth = 0,
    ellipsis
  } = props;

  const className = (ellipsis && `${classProp} uiKit-typography--text-ellipsis`) || classProp;

  switch (type) {
    case TypeTypographyEnum.h1:
      return (
        <H1 value={value} className={className} margin={margin} maxWidth={maxWidth}>
          {children}
        </H1>
      );
    case TypeTypographyEnum.h2:
      return (
        <H2 value={value} className={className} margin={margin} maxWidth={maxWidth}>
          {children}
        </H2>
      );
    case TypeTypographyEnum.h3:
      return (
        <H3 value={value} className={className} margin={margin} maxWidth={maxWidth}>
          {children}
        </H3>
      );
    case TypeTypographyEnum.h4:
      return (
        <H4 value={value} className={className} margin={margin} maxWidth={maxWidth}>
          {children}
        </H4>
      );
    case TypeTypographyEnum.large:
      return (
        <Large value={value} weight={weight} className={className} margin={margin} maxWidth={maxWidth}>
          {children}
        </Large>
      );
    case TypeTypographyEnum.body:
      return (
        <Body value={value} weight={weight} className={className} margin={margin} maxWidth={maxWidth}>
          {children}
        </Body>
      );
    case TypeTypographyEnum.small:
      return (
        <Small value={value} weight={weight} className={className} margin={margin} maxWidth={maxWidth}>
          {children}
        </Small>
      );
    default:
      return (
        <Body
          value={TypeTypographyEnum.body}
          weight={WeightTypographyEnum.regular}
          className={className}
          margin={margin}
          maxWidth={maxWidth}
        >
          {children}
        </Body>
      );
  }
};

