import React, { useMemo } from 'react';

import { Typography } from '../../Typography';

import { TypeLinkEnum, TypeLinkClassEnum, LinkProps } from './interfaces';

import './styles.css';

export const Link = (props: LinkProps): JSX.Element => {
  const { value, type = TypeLinkEnum.regular, className = '' } = props;

  // Memo
  const memoValue: string = useMemo(() => value, [value]);
  const memoIsExternalLink: boolean = useMemo(() => type === TypeLinkEnum.external, [type]);
  const memoClassName: string = useMemo(() => `uiKit-link-wrapper ${className}`, [className]);

  return (
    <div className={memoClassName}>
      <span className={TypeLinkClassEnum[type]} tabIndex={-1}>
        <Typography value={memoValue} />
      </span>
      {/* TODO настроить webpack для storybook на импорт svg */}
      {memoIsExternalLink && (
        <svg
          className="uiKit-link-typeExternal-arrow"
          width="16"
          height="17"
          viewBox="0 0 16 17"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M5.74994 4.00797C5.33573 4.00797 4.99994 4.34375 4.99994 4.75797C4.99994 5.17218 5.33573 5.50797 5.74994 5.50797H9.4533L3.49994 11.4473C3.20705 11.7402 3.20705 12.2151 3.49994 12.508C3.79283 12.8009 4.2677 12.8009 4.5606 12.508L10.5 6.58257V10.258C10.5 10.6722 10.8358 11.008 11.25 11.008C11.6642 11.008 12 10.6722 12 10.258V4.75797C12 4.34375 11.6642 4.00797 11.25 4.00797H5.74994Z"
            fill="#595A5F"
          />
        </svg>
      )}
    </div>
  );
};
