export interface LinkProps {
  /**
   * Value text
   * */
  value: string;
  /**
   * URL to redirect User
   * @default regular
   */
  type?: keyof typeof TypeLinkClassEnum;
  /**
   * Your className
   */
  className?: string;
}

export enum TypeLinkClassEnum {
  // External Link
  external = 'uiKit-link-typeExternal',
  // Pseudo Link
  pseudo = 'uiKit-link-typePseudo',
  // Regular Link
  regular = 'uiKit-link-typeRegular',
}

export enum TypeLinkEnum {
  external = 'external',
  pseudo = 'pseudo',
  regular = 'regular',
}
