import {
  createElement,
  forwardRef,
  Ref,
  useCallback,
  useState,
  FocusEvent,
  KeyboardEvent
} from 'react';

import { InputDynamicProps, InputDynamicTag, InputDynamicType, KeyName, InputAutoCompleteType } from './interfaces';
import { parseValue } from './utils';

import './styles.css';

export const InputDynamic = forwardRef(({
  className = '',
  tag = InputDynamicTag.input,
  type = InputDynamicType.text,
  value,
  checked,
  hasError,
  hasDirty = true,
  autoComplete = InputAutoCompleteType.off,
  onFocus,
  onPressEnter,
  onPressEsc,
  onPressArrowUp,
  onPressArrowDown,
  ...rest
}: InputDynamicProps, ref: Ref<HTMLInputElement | null>) => {
  const [dirty, setDirty] = useState(hasDirty);

  const isError = dirty && hasError;

  const handleFocus = useCallback((e: FocusEvent<HTMLInputElement>) => {
    setDirty(true);
    onFocus?.(e);
  }, [onFocus]);

  const handleKeyDown = useCallback((e: KeyboardEvent<HTMLInputElement>) => {
    const { key } = e;

    // eslint-disable-next-line default-case
    switch (key) {
      case KeyName.Enter:
        onPressEnter?.(e);
        break;
      case KeyName.Escape:
        onPressEsc?.(e);
        break;
      case KeyName.ArrowUp:
        onPressArrowUp?.(e);
        break;
      case KeyName.ArrowDown:
        onPressArrowDown?.(e);
        break;
    }
  }, [onPressEnter, onPressEsc, onPressArrowUp, onPressArrowDown]);

  return createElement(tag, {
    autoComplete,
    ref,
    type,
    className: `uiKit-input-dynamic uiKit-input-dynamic--${tag} uiKit-input-dynamic--${type} ${className} ${(isError && 'uiKit-input-dynamic--error') || ''}`,
    value: parseValue(value),
    checked: Boolean(checked),
    onFocus: handleFocus,
    onKeyDown: handleKeyDown,
    ...rest
  });
});
