export interface ButtonProps {
  /**
   * onClick
   * */
  onClick?: React.MouseEventHandler<HTMLElement>;
  /**
   * HandleCallback
   * */
  handleClick?: (value: React.MouseEvent<HTMLElement>) => void;
  /**
   * Value text
   * */
  value: string;
  /**
   * Disabled button props
   * @default false
   * */
  isDisabled?: boolean;
  /**
   * Disabled button if loading
   * @default false
   * */
  isLoading?: boolean;
  /**
   * Size, either primary/default/danger from SizeButtonClassEnum.
   * @default default
   * */
  type?: keyof typeof TypeButtonClassEnum;
  /**
   * Type button, either primary/default/danger from TypeButtonClassEnum.
   * @default regular
   * */
  size?: keyof typeof SizeButtonClassEnum;
  /**
   * Props for adding your class
   * @default ''
   * */
  className?: string;
}

export enum TypeButtonEnum {
  danger = 'danger',
  default = 'default',
  primary = 'primary',
  text = 'text'
}

export enum TypeButtonClassEnum {
  danger = 'uiKit-button-typeDanger',
  default = 'uiKit-button-typeDefault',
  primary = 'uiKit-button-typePrimary',
  text = 'uiKit-button-typeText'
}

export enum SizeButtonEnum {
  large = 'large',
  regular = 'regular',
  small = 'small',
}

export enum SizeButtonClassEnum {
  large = 'uiKit-button-sizeLarge',
  regular = 'uiKit-button-sizeRegular',
  small = 'uiKit-button-sizeSmall',
}
