import React from 'react';

import { Typography } from '../../../Typography';
import { Option } from '../interfaces';

import { ListItemsProps } from './interfaces';

import './styles.css';

export function ListItems({
  options,
  onSelect,
  selectedLabel,
  arrowIndex,
}: ListItemsProps) {
  return (
    <div className="uiKit-dropdown-popup">
      {
        (options.length && options.map((option: Option, i: number) => {
          const { label } = option;

          return (
            <div
              key={`${label}-${i + 1}`}
              role="button"
              tabIndex={0}
              className={`uiKit-dropdown-popup__item uiKit-dropdown-popup__item--option ${(arrowIndex && arrowIndex === i && 'uiKit-dropdown-popup__item--focus') || ''}`}
              onMouseDown={() => onSelect(option)}
            >
              <div className={`uiKit-dropdown-popup__item-label ${(selectedLabel === label && 'uiKit-dropdown-popup__item-label--selected') || ''}`} />
              <Typography value={label} />
            </div>
          );
        }))
        || (
          <div className="uiKit-dropdown-popup__item uiKit-dropdown-popup__item--empty">
            <Typography value="Ничего не найдено" />
          </div>
        )
      }
    </div>
  );
}
