import React from 'react';
import isEqual from 'lodash.isequal';

import { Typography } from '../../../TypographyDeprecated';
import { Option } from '../interfaces';

import { ListItemsProps } from './interfaces';

import './styles.css';

export function ListItems<T>({
  options,
  handleSelect,
  selectedValue,
  arrowIndex,
  name
}: ListItemsProps<T>) {
  return (
    <div className="uiKit-dropDown-listItemsWrapper">
      {(options.length
        && options.map((option: Option, index: number) => {
          const { label, value } = option;
          const isSelectedClassName: string = ((selectedValue === label
            || selectedValue === value
            || isEqual(selectedValue, value))
            && 'uiKit-dropDown-selected')
            || 'uiKit-dropDown-notSelected';
          const optionClassName: string = (arrowIndex !== null && arrowIndex === index && 'uiKit-dropDown-item_focus')
            || 'uiKit-dropDown-item';
          return (
            <div
              role="button"
              tabIndex={0}
              className={optionClassName}
              key={`${label}-${index + 1}`}
              // @ts-ignore
              name={name}
              onMouseDown={handleSelect(option, name)}
            >
              <div className={isSelectedClassName} />
              <Typography value={label} />
            </div>
          );
        }))
        || (
          <div className="uiKit-dropDown-notFound">
            <Typography value="Ничего не найдено" />
          </div>
        )}
    </div>
  );
}
