
import React, { forwardRef, Ref } from 'react';

import { Typography } from '../../Typography';
import { SimpleIcon } from '../../Icons/SimpleIcon';
import { InputDynamic, InputDynamicType } from '../common/InputDynamic';

import { InputBaseProps, IconSideEnum } from './interfaces';

import './styles.css';

export const InputBase = forwardRef(({
  mark,
  className = '',
  icon,
  type = InputDynamicType.text,
  iconSide = IconSideEnum.right,
  onIconClick,
  ...rest
}: InputBaseProps, ref: Ref<HTMLInputElement>) => {
  const inputClass = ((icon || mark) && `uiKit-input-base__field--icon-${iconSide}`) || '';

  const iconClass = `uiKit-input-base__icon uiKit-input-base__icon--${iconSide} ${(onIconClick && 'uiKit-input-base__icon--clickable') || ''}`;

  return (
    <div className={`uiKit-input-base ${className}`}>
      <InputDynamic
        ref={ref}
        type={type}
        {...rest}
        className={inputClass}
      />
      <div className={iconClass} onClick={onIconClick}>
        {
          (icon && <SimpleIcon type={icon} />)
          || (mark && <Typography value={mark} />)
        }
      </div>
    </div>
  );
});
