import React, { useCallback } from 'react';

import { Typography } from '../../TypographyDeprecated';
import { TypeTypographyEnum, WeightTypographyEnum } from '../../TypographyDeprecated/interfaces';
import { getRandomNumber } from '../../../utils/getRandomNumber';

import { TextareaProps } from './interfaces';
import './styles.css';

export function Textarea<T>({
  name,
  className = '',
  placeholder,
  value = '',
  handleChange,
  onChange,
  onChangeField,
  hasError = false,
  disabled = false,
  label = '',
  htmlFor = false
}: TextareaProps<T>) {
  const memoClassName = `uiKit-textArea ${(hasError && 'hasError') || ''} ${className}`;

  const handleCallback = useCallback(
    (event: React.ChangeEvent<HTMLTextAreaElement>) => {
      const { value } = event.target;

      handleChange?.(event);
      onChange?.(value);
      onChangeField?.(value, name as T);
    },
    [handleChange, onChange, onChangeField, name],
  );

  const forId = (htmlFor && label.length > 0 && `${getRandomNumber()}`) || '';

  return (
    <div className="uiKit-textArea-wrapper">
      {label.length > 0 && (
        <label htmlFor={forId} className="uiKit-textArea-label">
          <Typography
            value={label}
            type={TypeTypographyEnum.small}
            weight={WeightTypographyEnum.bold}
            margin="0 0 4px 12px"
          />
        </label>
      )}
      <textarea
        name={name as unknown as string | undefined}
        id={forId}
        className={memoClassName}
        placeholder={placeholder}
        disabled={disabled}
        value={value}
        onChange={handleCallback}
      />
    </div>
  );
}
