import React, { useMemo } from 'react';

import { IllustrationProps, TypeIllustrationClassEnum } from './interfaces';
import { DEFAULT_ILLISTRATION_SIZE } from './constants';

import './styles.css';

export const Illustrations = (props: IllustrationProps): JSX.Element => {
  const {
    type, width = 'auto', height = 'auto', className = ''
  } = props;

  // Memo
  const memoClassName: string = useMemo(
    () => `uiKit-illustration ${TypeIllustrationClassEnum[type]} ${className}`,
    [type],
  );

  const styles = useMemo(
    () => ({
      width: width || DEFAULT_ILLISTRATION_SIZE,
      height: height || DEFAULT_ILLISTRATION_SIZE,
      backgroundSize: `${width || DEFAULT_ILLISTRATION_SIZE} ${
        height || DEFAULT_ILLISTRATION_SIZE
      }`,
    }),
    [width, height],
  );

  return <div style={styles} className={memoClassName} />;
};
