import React, { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import isEqual from 'lodash.isequal';

import { Input } from '../Input';
import { Typography } from '../../TypographyDeprecated';
import { TypeTypographyEnum, WeightTypographyEnum } from '../../TypographyDeprecated/interfaces';
import { TypeSimpleIconEnum } from '../../Icons/SimpleIcon/interfaces';

import { ListItems } from './ListItems';
import { DropdowProps, Option } from './interfaces';
import { ArrowUpKeyCode, ArrowDownKeyCode } from './constants';

import './styles.css';

export function Dropdown<T>({
  htmlFor,
  placeholder,
  value,
  handleSelect,
  onChange,
  onChangeField,
  hasError = false,
  disabled = false,
  options,
  name,
  className = '',
  disableFilter = false,
  label = '',
  ref,
}: DropdowProps<T>) {
  const inputRef = useRef<string | undefined>(null);

  const findValue = useCallback(
    (value: any): string => options.find((option) => {
      if (option.value === value || isEqual(option.value, value)) return option.value || option.label || '';
      return false;
    })?.label || '',
    [options],
  );
  // useState
  const [searchValue, changeSearchValue] = useState<string>(findValue(value));
  const [isOpenList, changeIsOpenList] = useState<boolean>(false);
  const [isNotChangeValue, toogleIsNotChangeValue] = useState<boolean>(true);
  const [selectedValue, changeSelectedValue] = useState<string>(findValue(value));
  const [arrowIndex, changeArrowIndex] = useState<number | null>(null);

  const filtredOptions: Option[] = useMemo(() => {
    if (!disableFilter && searchValue?.length > 0 && !isNotChangeValue) {
      return options.filter((option: Option) => {
        const { label, value } = option;
        const lowerCaseSearchValue = searchValue.toLowerCase();
        if (
          label.toLowerCase().indexOf(lowerCaseSearchValue) !== -1
          || (typeof value === 'string' && value.toLowerCase().indexOf(lowerCaseSearchValue) !== -1)
        ) {
          return option;
        }
        return false;
      });
    }
    return options;
  }, [disableFilter, searchValue, isNotChangeValue, options]);

  const maxLength: number = useMemo(() => filtredOptions?.length - 1 || 0, [filtredOptions]);

  // useCallback
  const handleFilter = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>): void => {
      const { value } = event.target;
      event?.preventDefault();
      if (!disableFilter && typeof value === 'string') {
        changeSearchValue(value);
      }
      if (!disableFilter && isNotChangeValue) {
        toogleIsNotChangeValue(false);
      }
    },
    [disableFilter, isNotChangeValue],
  );

  useEffect(() => {
    changeSearchValue(findValue(value));
  }, [findValue, value]);

  // выбор опции через клик
  const handleSelectCallback = useCallback(
    (option: Option, name?: T) => () => {
      const { label, value } = option;
      changeSelectedValue(label);
      changeSearchValue(label);
      handleSelect?.(value, name as T);

      onChange?.(value);
      onChangeField?.(value, name as T);

      changeArrowIndex(null);

      if (!disableFilter && !isNotChangeValue) {
        toogleIsNotChangeValue(true);
      }
    },
    [handleSelect, onChange, onChangeField, disableFilter, isNotChangeValue],
  );

  // открытие с клавиатуры options и выбор option через enter
  const handlePressEnter = useCallback(
    (
      event: React.MouseEvent<HTMLDivElement, MouseEvent> | React.KeyboardEvent<HTMLInputElement>,
    ): void => {
      if ('preventDefault' in event) {
        event.preventDefault();
      }
      if (isOpenList === false && disabled !== true) {
        changeIsOpenList(true);
      }
      if (isOpenList === true && arrowIndex !== null && filtredOptions[arrowIndex]) {
        const { label, value } = filtredOptions[arrowIndex];
        changeIsOpenList(false);
        changeSelectedValue(label);
        changeSearchValue(label);
        handleSelect?.(value, name as T);

        onChange?.(value);
        onChangeField?.(value, name as T);
        changeArrowIndex(null);
        // @ts-ignore
        event.target.blur();
        if (!disableFilter && !isNotChangeValue) {
          toogleIsNotChangeValue(true);
        }
      }
    },
    [isOpenList, disabled, arrowIndex, filtredOptions, handleSelect, name, onChange, onChangeField, disableFilter, isNotChangeValue],
  );

  // закрытие меню options
  const handleClose = useCallback(() => {
    if (isOpenList === true) {
      changeSelectedValue((currentValue: string): string => {
        changeSearchValue(currentValue);
        return currentValue;
      });
      changeIsOpenList(false);
      toogleIsNotChangeValue(true);
      changeArrowIndex(null);
    }
  }, [changeSelectedValue, changeIsOpenList, isOpenList]);

  // Навигация по options с помошью стрелок
  const handlePressArrow = useCallback(
    (event: React.KeyboardEvent<HTMLInputElement>): void => {
      if (isOpenList) {
        if (event.keyCode === ArrowDownKeyCode) {
          changeArrowIndex((current) => {
            if (current === null || current === 0 || current > maxLength) return maxLength;
            return current - 1;
          });
        }
        if (event.keyCode === ArrowUpKeyCode) {
          changeArrowIndex((current) => {
            if (current === null || current === maxLength || current > maxLength) return 0;
            return current + 1;
          });
        }
      }
    },
    [changeArrowIndex, isOpenList, maxLength],
  );

  return (
    <div className={`uiKit-wrapper ${className}`}>
      {label.length > 0 && (
        <div className="uiKit-select-label">
          <Typography
            value={label}
            type={TypeTypographyEnum.small}
            weight={WeightTypographyEnum.bold}
          />
        </div>
      )}
      <div
        role="button"
        tabIndex={-1}
        ref={ref}
        className="uiKit-select-wrapper"
        onClick={handlePressEnter}
        onKeyDown={undefined}
      >
        <Input
          htmlFor={htmlFor}
          name={name}
          className="uiKit-select-input"
          // @ts-ignore
          ref={inputRef}
          hasError={hasError}
          disabled={disabled}
          value={searchValue}
          handleChange={handleFilter}
          placeholder={placeholder}
          onBlur={handleClose}
          onPressEnter={handlePressEnter}
          onPressEsc={handleClose}
          onPressArrow={handlePressArrow}
          icon={(isOpenList
            && TypeSimpleIconEnum.angleUp)
            || TypeSimpleIconEnum.angleDown}
        />
        {isOpenList && (
          <ListItems<T>
            name={name}
            options={filtredOptions}
            handleSelect={handleSelectCallback}
            selectedValue={selectedValue}
            arrowIndex={arrowIndex}
          />
        )}
      </div>
    </div>
  );
}
