import React, { useMemo } from 'react';

import { BodyProps, BodyClassNameEnum, BodyWeightEnum } from './interfaces';

import '../../../../styles/prplMainFonts.css';
import './styles.css';

const Body = (props: BodyProps): JSX.Element => {
  const {
    value, weight = BodyWeightEnum.regular, children, className = '', margin, maxWidth
  } = props;

  const memoValue: string = useMemo(() => value || '', [value]);
  const memoClassName: string = useMemo(
    () => `${BodyClassNameEnum[weight] || BodyWeightEnum.regular}${(maxWidth && '_eclipses') || ''} ${className}`,
    [className, maxWidth, weight],
  );

  const style = { margin: `${margin}`, maxWidth: (maxWidth && `${maxWidth}px`) || 'auto' };

  return (
    <p style={style} className={memoClassName}>
      {memoValue}
      {children}
    </p>
  );
};

export { Body };
