import { createElement, CSSProperties, FC } from 'react';

import './styles.css';
import { TypographyType, TypographyProps, TypographyTag } from './interfaces';

export const Typography: FC<TypographyProps> = ({
  type = TypographyType.body,
  weight,
  value,
  children,
  className = '',
  margin,
  ellipsis,
  multilineEllipsis,
  tag = TypographyTag.p,
  font
}) => {
  const classes = `uiKit-typography--${type} ${(ellipsis && 'uiKit-typography--text-ellipsis')} ${(font && `uiKit-typography--${font}`) || ''} ${(weight && `uiKit-typography--${weight}`) || ''} ${(multilineEllipsis && 'uiKit-typography--text-ellipsis-multiline') || ''}`;

  const style: CSSProperties = { margin, WebkitLineClamp: multilineEllipsis };

  return createElement(tag, {
    className: `uiKit-typography ${className} ${classes}`,
    children: children || value,
    style
  });
};

