import { getRandomNumber } from '../../../../utils/getRandomNumber';

import { eventManager } from './eventManager';
import { ToastProps, Message } from './interfaces';

export const toast = (props: ToastProps): void => {
  const { messages = null, type, id = getRandomNumber() } = props;

  if (typeof messages === 'string') {
    const messageObject: Message = { type, id, message: messages };
    eventManager.emit('show', messageObject);
  } else if (Array.isArray(messages)) {
    const messageObject: Message = { type, id, message: messages.join(' ') };
    eventManager.emit('show', messageObject);
  }
};
