import React, { useMemo } from 'react';

import { CircleColorEnum, CircleSizeEnum, CircleClassEnum, CircleProps } from './interfaces';
import yellowCircle from './assets/yellowCircle.svg';
import blackCircle from './assets/blackCircle.svg';

import './styles.css';

export const Circle = (props: CircleProps): JSX.Element => {
  const { size = CircleSizeEnum.regular, color = CircleColorEnum.black, className = '' } = props;

  // Memo
  const memoClassName: string = useMemo(
    () => `${CircleClassEnum[size]}-${color} ${className}`,
    [size, color, className],
  );
  const memoSrc: string = useMemo(
    () => (color === CircleColorEnum.black && blackCircle) || yellowCircle,
    [color],
  );

  return <img className={memoClassName} src={memoSrc} alt="" />;
};
