import React, { useMemo } from 'react';

import { LargeProps, LargeClassNameEnum, LargeWeightEnum } from './interfaces';

import '../../../../styles/prplMainFonts.css';
import './styles.css';

const Large = (props: LargeProps): JSX.Element => {
  const {
    value, weight = LargeWeightEnum.regular, children, className = '', margin, maxWidth
  } = props;

  const memoValue: string = useMemo(() => value || '', [value]);
  const memoClassName: string = useMemo(
    () => `${LargeClassNameEnum[weight] || LargeClassNameEnum.regular}${(maxWidth && '_eclipses') || ''} ${className}`,
    [weight, maxWidth, className],
  );

  const style = { margin: `${margin}`, maxWidth: (maxWidth && `${maxWidth}px`) || 'auto' };

  return (
    <p style={style} className={memoClassName}>
      {memoValue}
      {children}
    </p>
  );
};

export { Large };
