export interface SimpleIconProps {
  /**
     * Type Icon
     */
  type: keyof typeof TypeSimpleIconEnum;
  /**
     * Fill color for icon
     * @default 'black';
     */
  fill?: string;
  /**
     * Your class
     */
  className?: string;
  /**
    * Width
    * @default 16
    */
  width?: number;
  /**
    * Height
    * @default 16
    */
  height?: number;
  /**
    * ClickHandler
    */
  onClick?: () => void;
}

