import React, { useMemo } from 'react';

import { PagingItem } from '../PagingItem/intex';
import { SHIFT_LIMIT, MAX_LIMIT, DOTS_LIMIT, OVERFLOW_LIMIT } from '../constants';

import { PagingRowProps } from './interfaces';

import './styles.css';

export const PagingRow = (props: PagingRowProps): JSX.Element => {
  const { maxPage, currentPage, handleSelect } = props;

  const firstPage: number = 2;
  const lastPage = maxPage;
  const listPage = useMemo(() => {
    const list: number[] = [];
    for (let i = firstPage; i < lastPage; i++) {
      list.push(i);
    }
    return list;
  }, [lastPage]);

  const memoStylesOverflow = useMemo(() => {
    const width = 32;
    const padding = 16;
    let value = 0;
    const widthBlock = 'calc(32px * 5 + 8px * 5 * 2)';

    // Front
    if (currentPage === 1) {
      value = 0;
    }
    if (currentPage === DOTS_LIMIT) {
      value = currentPage - SHIFT_LIMIT;
    }
    if (currentPage > DOTS_LIMIT && currentPage < maxPage - DOTS_LIMIT) {
      value = currentPage - DOTS_LIMIT;
    }

    // End
    if (currentPage === maxPage || currentPage >= maxPage - SHIFT_LIMIT) {
      value = maxPage - MAX_LIMIT;
    }
    if (currentPage === maxPage - DOTS_LIMIT) {
      value = currentPage - DOTS_LIMIT;
    }

    // Limit
    if (maxPage < MAX_LIMIT) {
      value = 0;
    }

    const style = {
      left: `-${value * (width + padding)}px`,
      width: widthBlock,
    };

    return style;
  }, [currentPage, maxPage]);

  const memoStylesWrapper = useMemo(() => {
    let width = 'calc(32px * 5 + 8px * 5 * 2)';
    if (maxPage < MAX_LIMIT) {
      width = `calc(32px * ${maxPage - OVERFLOW_LIMIT} + 8px * ${maxPage - OVERFLOW_LIMIT} * 2)`;
    }
    const style = { width };
    return style;
  }, [maxPage]);

  return (
    <div style={memoStylesWrapper} className="uiKit-paging-row-wrapper">
      <div style={memoStylesOverflow} className="uiKit-paging-row-overflow">
        {listPage.map((item, index) => {
          const key: string = `uiKit-paging-row-${index}`;
          return (
            <PagingItem
              key={key}
              active={item === currentPage}
              value={String(item)}
              handleSelect={handleSelect(item)}
            />
          );
        })}
      </div>
    </div>
  );
};
