import React, { useCallback, FC } from 'react';

import { Typography } from '../../Typography';
import { InputDynamic } from '../common/InputDynamic';

import { RadioProps } from './interfaces';
import './styles.css';

export const Radio: FC<RadioProps> = ({
  label,
  disabled,
  children,
  className = '',
  onChange,
  ...rest
}) => {
  const handleChange = useCallback((e) => {
    onChange?.(e.target.value);
  }, [onChange]);

  return (
    <label className={`uiKit-radio ${className} ${(disabled && 'uiKit-radio--disabled') || ''}`}>
      <InputDynamic
        {...rest}
        disabled={disabled}
        type="radio"
        onChange={handleChange}
      />

      {children || (label && <Typography value={label} />)}
    </label>
  );
};
