export interface TooltipProps {
  /**
   * Text value for tooltip
   */
  value: string;
  /**
   * Type for tooltip
   * @default 'bottomLeft'
   */
  type?: keyof typeof TooltipClassEnum;
  /**
   * Your class for position tooltip
   */
  className?: string;
}

export enum TooltipClassEnum {
  bottomCenter = 'uiKit-tooltip-arrowBottomCenter',
  bottomLeft = 'uiKit-tooltip-arrowBottomLeft',
  bottomRight = 'uiKit-tooltip-arrowBottomRight',
  left = 'uiKit-tooltip-arrowLeft',
  right = 'uiKit-tooltip-arrowRight',
  topCenter = 'uiKit-tooltip-arrowTopCenter',
  topLeft = 'uiKit-tooltip-arrowTopLeft',
  topRight = 'uiKit-tooltip-arrowTopRight',
}

export enum TooltipTypeEnum {
  bottomCenter = 'bottomCenter',
  bottomLeft = 'bottomLeft',
  bottomRight = 'bottomRight',
  left = 'left',
  right = 'right',
  topCenter = 'topCenter',
  topLeft = 'topLeft',
  topRight = 'topRight',
}
