import React, { useMemo } from 'react';

import { Typography } from '../../Typography';

import { TooltipProps, TooltipClassEnum, TooltipTypeEnum } from './interfaces';

import './styles.css';

export const Tooltip = (props: TooltipProps): JSX.Element => {
  const { value, type = TooltipTypeEnum.bottomLeft, className = '' } = props;

  // useMemo
  const tooltipArrowClassName: string = useMemo(() => `${TooltipClassEnum[type]}`, [type]);
  const memoClassName: string = useMemo(() => `uiKit-tooltipWrapper ${className}`, [className]);

  return (
    <div className={memoClassName}>
      <div className="uiKit-tooltip">
        <Typography
          type="small"
          weight="regular"
          value={value}
        />
        <div className={tooltipArrowClassName} />
      </div>
    </div>
  );
};
