import React, { useMemo } from 'react';

import { Typography } from '../../Typography';

import { BadgeProps } from './interfaces';
import { MAX_LIMIT_BADGE } from './constants';

import './styles.css';

export const Badge = (props: BadgeProps): JSX.Element => {
  const { value, className = '' } = props;

  // Memo
  const memoValue: string = useMemo(
    () => (value <= MAX_LIMIT_BADGE && `${value}`) || '99+',
    [value],
  );
  const memoClassName: string = useMemo(() => `uiKit-badge ${className}`, [className]);
  return (
    <div className={memoClassName}>
      <Typography type="small">{memoValue}</Typography>
    </div>
  );
};
