import { dayjs, YYYYMMDDHHmmss } from '../../../lib/dayjs';

export const checkDate = (date?: string | null | Date) => {
  if (date) {
    const parcedDate = dayjs(date);

    if (parcedDate.isValid() && parcedDate.valueOf() > 0) {
      return parcedDate.toDate();
    }
  }

  return null;
};

export const prepareDate = (date: Date | null) => (checkDate(date) && dayjs(date).format(YYYYMMDDHHmmss)) || '';
