
import React, { useCallback, useEffect, useMemo, useState } from 'react';
// @ts-ignore
import { IMaskInput } from 'react-imask';

import { Typography } from '../../TypographyDeprecated';
import { TypeTypographyEnum, WeightTypographyEnum } from '../../TypographyDeprecated/interfaces';
import { SimpleIcon } from '../../Icons/SimpleIcon';
import { TypeSimpleIconEnum } from '../../Icons/SimpleIcon/interfaces';
import { getRandomNumber } from '../../../utils/getRandomNumber';

import { InputProps, ValidationOption, IconClassEnum, IconSideEnum } from './interfaces';
import { ArrowDownKeyCode, ArrowUpKeyCode, EnterKeyCode, EscKeyCode } from './constants';

import './styles.css';

export function Input<T>({
  className = '',
  placeholder,
  value = '',
  handleChange,
  hasError = false,
  hint = '',
  disabled = false,
  onPressEnter,
  onPressEsc,
  onPressArrow,
  onFocus,
  onBlur,
  name,
  onChange,
  onChangeField,
  label = '',
  validation = [],
  withMask = undefined,
  icon,
  iconSide = IconSideEnum.right,
  htmlFor = false,
}: InputProps<T>) {
  // State
  const [hintMessage, setHintMessage] = useState<string>(hint);

  const convertValue = (typeof value === 'string' && value) || String(value);

  const validateInput = useCallback(() => {
    const resultValidation = validation?.find((rules: ValidationOption) => {
      const { test, errorMessage } = rules;

      if (!test.test(convertValue)) {
        return errorMessage;
      }
      return undefined;
    });
    return resultValidation;
  }, [convertValue, validation]);

  // Effect
  useEffect(() => {
    if (validation.length) {
      const resultValidation = validateInput();

      const errorMessage = resultValidation?.errorMessage || '';
      const message = (convertValue.length && errorMessage) || hint;
      setHintMessage(message);
    }
  }, [convertValue, validation, validateInput, hint]);

  // Memo
  const memoClassName = useMemo(() => {
    const isError: boolean = hasError || (hintMessage !== hint && Boolean(hintMessage.length));
    return `uiKit-input-wrapper ${(isError && 'hasError') || ''} ${(disabled && 'hasDisabled') || ''} ${className} ${(icon && 'withIcon') || ''}`;
  }, [hasError, hintMessage, hint, disabled, className, icon]);

  const inputClass = (icon && `uiKit-input ${iconSide}`) || 'uiKit-input';

  // Callbacks
  const handleCallback = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const { value } = event.target;
      // @ts-ignore
      const { mask } = event;
      const resultValidation = validateInput();

      handleChange?.(event, resultValidation);
      onChangeField?.(value, name as T, resultValidation, mask);
      onChange?.(value, resultValidation, mask);
    },
    [handleChange, name, onChange, onChangeField, validateInput],
  );

  const handleAccept = useCallback((val, mask, event) => {
    // eslint-disable-next-line no-param-reassign
    event.mask = mask;
    handleCallback(event);
  }, [handleCallback]);

  const handleKeyDown = useCallback(
    (event: React.KeyboardEvent<HTMLInputElement>): void => {
      if (onPressEnter && event.keyCode === EnterKeyCode) {
        onPressEnter(event);
      }
      if (onPressEsc && event.keyCode === EscKeyCode) {
        onPressEsc(event);
      }
      if (
        onPressArrow
        && (event.keyCode === ArrowUpKeyCode || event.keyCode === ArrowDownKeyCode)
      ) {
        onPressArrow(event);
      }
    },
    [onPressArrow, onPressEnter, onPressEsc],
  );

  const handleFocus = useCallback(
    (event) => {
      // eslint-disable-next-line no-param-reassign
      event.target.autocomplete = 'whatever';

      // eslint-disable-next-line @typescript-eslint/no-unused-expressions
      if (onFocus) onFocus;
    },
    [onFocus],
  );

  const forId = (htmlFor && label.length > 0 && `${getRandomNumber()}`) || '';

  return (
    <div className={memoClassName}>
      {label.length > 0 && (
        // eslint-disable-next-line jsx-a11y/label-has-associated-control
        <label htmlFor={forId} className="uiKit-input-label">
          <Typography
            value={label}
            type={TypeTypographyEnum.small}
            weight={WeightTypographyEnum.bold}
            margin="0 0 4px 12px"
          />
        </label>
      )}
      <div className="uiKit-field-wrapper">
        {
          (withMask && (
            <IMaskInput
              {...withMask}
              // @ts-ignore
              autoComplete="off"
              id={forId}
              className={inputClass}
              name={name as unknown as string | undefined}
              placeholder={placeholder}
              disabled={disabled}
              value={convertValue}
              onAccept={handleAccept}
            />
          )) || (
            <input
              id={forId}
              autoComplete="off"
              name={name as unknown as string | undefined}
              className={inputClass}
              placeholder={placeholder}
              disabled={disabled}
              value={convertValue}
              onChange={handleCallback}
              onKeyDown={handleKeyDown}
              onFocus={handleFocus}
              onBlur={onBlur}
            />
          )
        }
        {(icon && icon in TypeSimpleIconEnum && (
          <SimpleIcon
            className={IconClassEnum[iconSide]}
            type={icon as keyof typeof TypeSimpleIconEnum}
          />
        )) || <Typography className={IconClassEnum[iconSide]} value={icon} />}
      </div>
      {hintMessage && (
        <span className="uiKit-input-hint">
          <Typography value={hintMessage} type={TypeTypographyEnum.small} />
        </span>
      )}
    </div>
  );
}
