
import React, { useCallback, ChangeEvent, useState, useEffect } from 'react';
import { InputMask, IMaskMixin } from 'react-imask';
import { v4 as uuid } from 'uuid';

import { InputBase } from '../InputBase';
import { parseValue } from '../common/InputDynamic';
import { FormField } from '../common/FormField';

import { InputProps } from './interfaces';

const InputMasked = IMaskMixin(({ inputRef, ...rest }) => <InputBase {...rest} ref={inputRef} />);

export const Input = <T,>({
  className = '',
  hint,
  error,
  name,
  label,
  withMask,
  htmlFor = true,
  value,
  hasDirty,
  disabled,
  hasError,
  inputRef,
  onChange,
  onChangeField,
  ...rest
}: InputProps<T>,) => {
  const [maskRef, setMaskRef] = useState<InputMask>();

  const changeField = useCallback((value: string, mask?: string) => {
    onChangeField?.(value, name as T, mask);
    onChange?.(value, mask);
  }, [name, onChange, onChangeField]);

  const handleChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    changeField(e.target.value);
  }, [changeField]);

  const handleAccept = useCallback((val: string, mask: InputMask) => {
    setMaskRef(mask);

    changeField(mask.unmaskedValue, val);
  }, [changeField]);

  const forId = (htmlFor && label && uuid()) || '';

  const typedName = name as string | undefined;

  useEffect(() => {
    if (withMask && maskRef) {
      maskRef.value = parseValue(value);
    }
  }, [value, maskRef, withMask]);

  return (
    <FormField
      className={className}
      label={label}
      forId={forId}
      hasDirty={hasDirty}
      hasError={hasError}
      disabled={disabled}
      hint={hint}
      error={error}
    >
      {
        (withMask && (
          <InputMasked
            {...rest}
            {...withMask}
            ref={inputRef}
            hasDirty={hasDirty}
            hasError={hasError}
            value={parseValue(value)}
            disabled={disabled}
            id={forId}
            name={typedName}
            onAccept={handleAccept}
          />
        )) || (
          <InputBase
            {...rest}
            ref={inputRef}
            hasDirty={hasDirty}
            disabled={disabled}
            hasError={hasError}
            value={value}
            id={forId}
            name={typedName}
            onChange={handleChange}
          />
        )
      }
    </FormField>
  );
};
