import React, { useMemo, useCallback, useRef } from 'react';

import { ModalLayerProps } from './interfaces';

import './styles.css';

export const ModalLayer = (props: ModalLayerProps): JSX.Element => {
  const { className = '', children, onMouseDown } = props;

  // Ref
  const wrapperRef = useRef(null);

  // Memo
  const memoClassName: string = useMemo(() => `uiKit-modalLayer ${className}`, [className]);

  // Callback
  const handleClose = useCallback(
    (event) => {
      // event.preventDefault();
      if (onMouseDown && event.target === wrapperRef.current) {
        onMouseDown(event);
      }
    },
    [onMouseDown],
  );
  return (
    <div
      role="button"
      tabIndex={0}
      ref={wrapperRef}
      className={memoClassName}
      onMouseDown={handleClose}
    >
      {children}
    </div>
  );
};
