import React, { useMemo, useRef, useEffect, useState, useCallback } from 'react';

import { Notification } from '../Notification';
import { NotificationSizeEnum } from '../Notification/interfaces';

import {
  AutoCloseNitificationType,
  AutoCloseNotificationProps,
  AutoCloseProgressClassEnum,
} from './interfaces';
import { default_time, time_interval } from './constants';

import './styles.css';

// TODO выкинуть ts-ignore
export const AutoCloseNotification = (props: AutoCloseNotificationProps): JSX.Element => {
  const {
    value,
    timeToClose = default_time,
    className = '',
    type = AutoCloseNitificationType.warning,
    id,
    closeCallback,
  } = props;

  // useRef
  const notificationRef = useRef(null);
  const progressionBarRef = useRef(null);
  const timerRef = useRef<any>(null);
  const animationRef = useRef<any>(null);

  // useState
  const [isVisible, toggleVisible] = useState<boolean>(true);
  const [timeCount, changeTime] = useState<number>(0);

  // useMemo
  const memoProgressBarClassName = useMemo(() => AutoCloseProgressClassEnum[type], [type]);
  const memoClassName = useMemo(
    () => `uiKit-autoCloseNotification-wrapper ${className}`,
    [className],
  );

  // useEffect
  useEffect(() => {
    // Эффект старта таймаута + очистки на didmount
    if (timerRef.current) clearInterval(timerRef.current);
    if (progressionBarRef.current) {
      // @ts-ignore
      animationRef.current = progressionBarRef.current.animate(
        [{ width: '100%' }, { width: '0%' }],
        {
          duration: timeToClose,
          fill: 'forwards',
        },
      );
    }
    timerRef.current = setInterval(() => {
      changeTime((currentTime) => currentTime + time_interval);
    }, time_interval);
    return () => clearInterval(timerRef.current);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (timeCount === timeToClose) {
      toggleVisible(() => {
        if (closeCallback && id !== undefined) {
          closeCallback(id);
        }
        return false;
      });
    }
  }, [timeCount, id, closeCallback, timeToClose]);

  // Эффект очистки таймаута если пользователь закрыл уведомление
  useEffect(() => {
    // @ts-ignore
    if (notificationRef?.current?.children[0] === undefined) {
      clearInterval(timerRef.current);
    }
    // @ts-ignore
  }, [notificationRef?.current?.children[0]]);

  // Callback
  const handleMouseEnter = useCallback(() => {
    clearInterval(timerRef.current);
    timerRef.current = null;
    animationRef?.current?.pause();
  }, [timerRef.current, animationRef.current]);

  const handleMouseOut = useCallback(() => {
    if (timerRef.current === null) {
      animationRef.current.play();
      timerRef.current = setInterval(() => {
        changeTime((currentTime) => currentTime + time_interval);
      }, time_interval);
    }
  }, [timerRef.current, animationRef.current]);

  return (
    <>
      {isVisible && (
        <div
          ref={notificationRef}
          className={memoClassName}
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseOut}
        >
          <Notification
            id={id}
            value={value}
            size={NotificationSizeEnum.regular}
            type={type}
            closeCallback={closeCallback}
          />
          <div ref={progressionBarRef} className={memoProgressBarClassName} />
        </div>
      )}
    </>
  );
};
