import React, { useCallback, useMemo } from 'react';
import ReactDatePicker from 'react-datepicker';
import ru from 'date-fns/locale/ru';

import { getRandomNumber } from '../../../utils/getRandomNumber';
import { FormField } from '../../Fields/common/FormField';

import { DatePickerProps } from './interfaces';
import { DateField } from './DateField';
import { checkDate, prepareDate } from './utils';

import 'react-datepicker/dist/react-datepicker.css';
import './styles.css';

export function DatePicker<T = undefined>({
  disabled,
  htmlFor = true,
  hasError,
  startDate,
  endDate,
  name,
  nameEnd,
  label,
  className = '',
  hasRange,
  hint,
  error,
  hasDirty,
  minDate,
  onChange,
  onChangeField,
  onChangePeriod,
}: DatePickerProps<T>) {
  const handleChangePeriod = useCallback((dates: [Date, Date]) => {
    const start = prepareDate(dates[0]);
    const end = prepareDate(dates[1]);

    onChangeField?.(start, name as T);
    onChangeField?.(end, nameEnd as T);

    onChangePeriod?.({ start, end });
  }, [name, nameEnd, onChangeField, onChangePeriod]);

  const handleChange = useCallback((date: Date) => {
    const formattedDate = prepareDate(date);

    onChange?.(formattedDate);
    onChangeField?.(formattedDate, name as T);
  }, [name, onChange, onChangeField]);

  const forId = (htmlFor && label && `${getRandomNumber()}`) || '';

  const dates = useMemo(() => {
    const selectedDate = checkDate(startDate);

    const selected = { minDate: checkDate(minDate), selected: selectedDate, placeholderText: 'ДД.ММ.ГГГГ', };

    if (hasRange) {
      return {
        ...selected,
        startDate: selectedDate,
        endDate: checkDate(endDate),
        placeholderText: 'ДД.ММ.ГГГГ - ДД.ММ.ГГГГ'
      };
    }

    return selected;
  }, [endDate, hasRange, minDate, startDate]);

  const changeHandler = useMemo(() => (hasRange && handleChangePeriod) || handleChange,
    [handleChange, handleChangePeriod, hasRange]);

  return (
    <FormField
      className={`uiKit-datepicker ${className}`}
      label={label}
      disabled={disabled}
      forId={forId}
      hasDirty={hasDirty}
      hasError={hasError}
      hint={hint}
      error={error}
    >

      <ReactDatePicker
        disabledKeyboardNavigation
        locale={ru}
        calendarStartDay={1}
        disabled={disabled}
        id={forId}
        dateFormat="d.MM.yyyy"
        {...dates}
        selectsRange={hasRange}
        customInput={<DateField hasDirty={hasDirty} hasError={hasError} />}
        onChange={changeHandler}
      />
    </FormField>
  );
}
