
import React, { FC, useCallback, useState } from 'react';

import { Typography } from '../../../Typography';

import { FormFieldProps } from './interfaces';

import './styles.css';

export const FormField: FC<FormFieldProps> = ({
  children,
  hasError,
  hasDirty = true,
  error,
  hint,
  forId,
  label,
  disabled,
  className = ''
}) => {
  const [dirty, setDirty] = useState(hasDirty);

  const isError = dirty && hasError && !disabled;
  const errorText = isError && error;

  const handleClick = useCallback(() => {
    setDirty(true);
  }, []);

  return (
    <div className={`uiKit-form-field  ${className}`} onClick={handleClick}>

      {label && (
        <label htmlFor={forId} className="uiKit-form-field__label">
          <Typography
            ellipsis
            value={label}
            type="small"
            weight="semibold"
            margin="0 0 4px 12px"
          />
        </label>
      )}

      <div className="uiKit-form-field__inner">
        {children}

        {(errorText || hint) && (
          <Typography
            className={`uiKit-form-field__hint ${(errorText && 'uiKit-form-field__error') || ''}`}
            value={errorText || hint}
            type="small"
          />
        )}
      </div>

    </div>
  );
};
