import React, { useCallback } from 'react';

import { CheckboxProps, TypeCheckboxEnumClassEnum, TypeCheckboxEnum } from './interfaces';

import './styles.css';

export function Checkbox<T>({
  name,
  isActive,
  hasError = false,
  disabled = false,
  type = TypeCheckboxEnum.normal,
  onClick,
  onChange,
  onChangeField,
  className = '',
}: CheckboxProps<T>) {
  // Memo
  const memoClassName = `${TypeCheckboxEnumClassEnum[type]}${(hasError && '_hasError') || ''} ${className}`;

  const handleChange = useCallback((e) => {
    const { checked } = e.target;

    onChange?.(checked);
    onChangeField?.(checked, name as T);
    onClick?.(e);
  }, [name, onChange, onChangeField, onClick]);

  return (
    <input
      className={memoClassName}
      type="checkbox"
      disabled={disabled}
      onChange={handleChange}
      defaultChecked={isActive}
    />
  );
}
