export interface BodyProps {
  value?: string | undefined;
  weight?: keyof typeof BodyClassNameEnum;
  className?: string | undefined;
  children: React.ReactNode;
  margin?: string;
  maxWidth?: number;
}

export enum BodyClassNameEnum {
  bold = 'uiKit-typography-body-bold',
  regular = 'uiKit-typography-body-regular',
  semiBold = 'uiKit-typography-body-semiBold',
}

export enum BodyWeightEnum {
  bold = 'bold',
  regular = 'regular',
  semiBold = 'semiBold',
}
