import React, { useMemo } from 'react';

import { MulticolorProps, TypeMulticolorClassEnum } from './interfaces';
import { DEFAULT_MULTICOLOR_SIZE } from './constants';

import './styles.css';

export const Multicolor = (props: MulticolorProps): JSX.Element => {
  const {
    type, width = 'auto', height = 'auto', className = ''
  } = props;

  // Memo
  const memoClassName: string = useMemo(
    () => `uiKit-multicolor ${TypeMulticolorClassEnum[type]} ${className}`,
    [type, className],
  );

  const styles = useMemo(
    () => ({
      width: width || DEFAULT_MULTICOLOR_SIZE,
      height: height || DEFAULT_MULTICOLOR_SIZE,
      backgroundSize: `${width || DEFAULT_MULTICOLOR_SIZE} ${height || DEFAULT_MULTICOLOR_SIZE}`,
    }),
    [width, height],
  );

  return <div style={styles} className={memoClassName} />;
};
