export interface TypographyProps {
  /**
   * Type Typography keyof TypeTypographyEnum
   * @default TypeTypographyEnum.body
   * */
  type?: keyof typeof TypeTypographyEnum;
  /**
   * Weight Typography keyof WeightTypographyEnum
   * @default WeightTypographyEnum.regular
   * */
  weight?: keyof typeof WeightTypographyEnum;
  /**
   * Value for Typography
   * */
  value?: string | undefined;
  /**
   * Your class name
   */
  className?: string;
  children?: React.ReactNode;
  /**
   * Margin
   * @default '0 0 0 0'
   */
  margin?: string;
  /**
   * Max-width work with css based on eclipses
   * @default 0
   */
  maxWidth?: number;
  /**
   * Hides text up to ellipsis
   * */
  ellipsis?: boolean;
}

export enum TypeTypographyEnum {
  body = 'body',
  h1 = 'h1',
  h2 = 'h2',
  h3 = 'h3',
  h4 = 'h4',
  large = 'large',
  small = 'small',
}

export enum WeightTypographyEnum {
  bold = 'bold',
  regular = 'regular',
  semiBold = 'semiBold',
}
