import React, { useMemo } from 'react';

import { H1props } from './interfaces';

import '../../../../styles/prplMainFonts.css';
import './styles.css';

const H1 = (props: H1props): JSX.Element => {
  const {
    value, children, className = '', margin, maxWidth
  } = props;

  const memoValue: string = useMemo(() => value || '', [value]);
  const memoClassName: string = useMemo(() => `uiKit-typography-h1${(maxWidth && '_eclipses') || ''} ${className}`, [className, maxWidth]);

  const style = { margin: `${margin}`, maxWidth: (maxWidth && `${maxWidth}px`) || 'auto' };

  return (
    <h1 style={style} className={memoClassName}>
      {memoValue}
      {children}
    </h1>
  );
};

export { H1 };
