import React, { useMemo } from 'react';

import { H3props } from './interfaces';

import '../../../../styles/prplMainFonts.css';
import './styles.css';

const H3 = (props: H3props): JSX.Element => {
  const {
    value, children, className = '', margin, maxWidth
  } = props;

  const memoValue: string = useMemo(() => value || '', [value]);

  const memoClassName: string = useMemo(() => `uiKit-typography-h3${(maxWidth && '_eclipses') || ''} ${className}`, [className, maxWidth]);

  const style = { margin: `${margin}`, maxWidth: (maxWidth && `${maxWidth}px`) || 'auto' };

  return (
    <h3 style={style} className={memoClassName}>
      {memoValue}
      {children}
    </h3>
  );
};

export { H3 };
