export interface LabelProps {
  /**
   * Value label
   * */
  value: string;

  /**
   * Size label
   * @default regular
   * */
  size?: keyof typeof LabeSizeEnum;

  /**
   * Type label
   * @default default
   * */
  type?: keyof typeof LabelClassEnum;

  /**
   * Your className
   */
  className?: string;
}

export enum LabelClassEnum {
  danger = 'uiKit-label-danger',
  default = 'uiKit-label-default',
  success = 'uiKit-label-success',
  warning = 'uiKit-label-warning',
}

export enum LabelTypeEnum {
  danger = 'danger',
  default = 'default',
  success = 'success',
  warning = 'warning',
}

export enum LabeSizeEnum {
  regular = 'regular',
  small = 'small',
}
