import React, { useCallback } from 'react';

import { Radio } from '../Radio';
import { FormField } from '../common/FormField';

import { RadioGroupProps } from './interfaces';
import { safeParse } from './utils';

import './styles.css';

export function RadioGroup<T>({
  name,
  hint,
  error,
  label,
  options,
  onChange,
  onChangeField,
  value,
  hasDirty,
  hasError, disabled,
  className = '',
  ...rest
}: RadioGroupProps<T>) {
  const handleChange = useCallback((value: string) => {
    const parsedVal = safeParse(value);

    onChange?.(parsedVal);
    onChangeField?.(parsedVal, name);
  }, [name, onChange, onChangeField]);

  return (
    <FormField
      className={className}
      label={label}
      hasDirty={hasDirty}
      hasError={hasError}
      disabled={disabled}
      hint={hint}
      error={error}
    >
      {options?.map((option, i) => {
        const val = `${option.value}`;

        return (
          <Radio
            {...rest}
            className="uiKit-radio-group"
            hasDirty={hasDirty}
            hasError={hasError}
            key={`${name}-${i + 1}`}
            disabled={disabled}
            name={name as unknown as string | undefined}
            value={val}
            label={option.label}
            checked={val === `${value}`}
            onChange={handleChange}
          />
        );
      })}
    </FormField>
  );
}
