import React, { useMemo } from 'react';

import { DotsProps, DotsSizeEnum, DotsClassEnum } from './interfaces';
import DotsSvg from './assets/index.svg';

import './styles.css';

export const Dots = (props: DotsProps): JSX.Element => {
  const { size = DotsSizeEnum.regular, className = '' } = props;

  // Memo
  const memoClassName: string = useMemo(
    () => `${DotsClassEnum[size]} ${className}`,
    [size, className],
  );

  return <img src={DotsSvg} className={memoClassName} alt="" />;
};
