import React, { useMemo } from 'react';

import { Typography, TypographyType } from '../../Typography';

import { LabelProps, LabelClassEnum, LabelTypeEnum, LabeSizeEnum } from './interfaces';

import './styles.css';

const Label = (props: LabelProps): JSX.Element => {
  const {
    value,
    size = LabeSizeEnum.regular,
    type = LabelTypeEnum.default,
    className = '',
  } = props;

  // Memo
  const memoValue: string = useMemo(() => value, [value]);
  const memoClassName: string = useMemo(
    () => `${LabelClassEnum[type]}${(size === LabeSizeEnum.regular && '_regular') || '_small'} ${className}`,
    [type, size, className],
  );
  const memoTypographyType: keyof typeof TypographyType = useMemo(
    () => (size === LabeSizeEnum.regular && TypographyType.body) || TypographyType.small,
    [size],
  );

  return (
    <div className={memoClassName}>
      <Typography
        type={memoTypographyType}
        weight="semibold"
        value={memoValue}
      />
    </div>
  );
};

export { Label };
