export interface SmallProps {
  value?: string | undefined;
  weight?: keyof typeof SmallClassNameEnum;
  children?: React.ReactNode;
  className?: string | undefined;
  margin?: string;
  maxWidth?: number;
}

export enum SmallClassNameEnum {
  bold = 'uiKit-typography-small-semiBold',
  regular = 'uiKit-typography-small-regular',
  semiBold = 'uiKit-typography-small-semiBold',
}

export enum SmallWeightEnum {
  bold = 'semiBold',
  regular = 'regular',
  semiBold = 'semiBold',
}
