import React, { useMemo, useCallback, useState } from 'react';

import { Dots } from '../../Preloaders/Dots';
import { Typography, TypographyType } from '../../Typography';
import { DotsSizeEnum } from '../../Preloaders/Dots/interfaces';

import {
  ButtonProps,
  TypeButtonClassEnum,
  SizeButtonClassEnum,
  SizeButtonEnum,
  TypeButtonEnum,
} from './interfaces';
import { DEFAULT_ANIMATION_TIMEOUT } from './constants';

import '../../../../styles/prplMainStyles.css';
import './styles.css';

// TODO какие-то изменения в макете по кнопке
export const Button = (props: ButtonProps): JSX.Element => {
  const {
    handleClick,
    value = '',
    isDisabled = false,
    isLoading = false,
    type = TypeButtonEnum.default,
    size = SizeButtonEnum.regular,
    className = '',
  } = props;
  // State //
  const [isAnimation, toggleAnimation] = useState<boolean>(false);

  // Memo //
  const memoDisabled: boolean = useMemo(() => isDisabled, [isDisabled]);
  const memoLoading: boolean = useMemo(() => isLoading, [isLoading]);

  const memoTypographyType = useMemo(() => {
    if (size === SizeButtonEnum.regular) {
      return TypographyType.body;
    }
    if (size === SizeButtonEnum.small) {
      return TypographyType.small;
    }
    if (size === SizeButtonEnum.large) {
      return TypographyType.large;
    }
  }, [size]);

  const buttonClassName = useMemo(
    () => `${TypeButtonClassEnum[type]} ${SizeButtonClassEnum[size]} ${(isAnimation && 'animation-Click') || ''} ${className}`,
    [type, size, className, isAnimation],
  );

  // Callbacks //
  const useCallbackHandleClick = useCallback(
    (event: React.MouseEvent<HTMLElement>): null | void => {
      if (memoDisabled || memoLoading) return null;
      const timeout = setTimeout(() => toggleAnimation(false), DEFAULT_ANIMATION_TIMEOUT);
      if (isAnimation) {
        clearTimeout(timeout);
      } else {
        toggleAnimation(true);
      }
      // eslint-disable-next-line @typescript-eslint/no-unused-expressions
      handleClick && handleClick(event);
    },
    [handleClick, isAnimation, memoDisabled, memoLoading],
  );

  return (
    <button
      type="button"
      className={buttonClassName}
      disabled={memoDisabled}
      onClick={useCallbackHandleClick}
    >
      {memoLoading && <Dots size={DotsSizeEnum[size]} />}
      <Typography value={value} type={memoTypographyType} weight="semibold" />
    </button>
  );
};
