import React, { useCallback, PropsWithChildren } from 'react';

import { Typography } from '../../Typography';
import { InputDynamic } from '../common/InputDynamic';
import { FormField } from '../common/FormField';

import { CheckboxProps } from './interfaces';
import './styles.css';

export const Checkbox = <T,>({
  name,
  label,
  content,
  children,
  className = '',
  disabled,
  hint,
  error,
  hasDirty,
  hasError,
  onChange,
  onChangeField,
  ...rest
}: PropsWithChildren<CheckboxProps<T>>) => {
  const handleChange = useCallback((e) => {
    const { checked } = e.target;

    onChange?.(checked);
    onChangeField?.(checked, name as T);
  }, [name, onChange, onChangeField]);

  return (
    <FormField
      className={className}
      label={label}
      hasDirty={hasDirty}
      hasError={hasError}
      hint={hint}
      error={error}
      disabled={disabled}
    >
      <label className={`uiKit-checkbox ${(disabled && 'uiKit-radio--disabled') || ''}`}>
        <InputDynamic
          {...rest}
          hasDirty={hasDirty}
          hasError={hasError}
          disabled={disabled}
          type="checkbox"
          onChange={handleChange}
        />

        {children || (content && <Typography value={content} />)}
      </label>
    </FormField>
  );
};
