// @ts-ignore
import { AnyMaskedOptions } from 'imask';

import { TypeSimpleIconEnum } from '../../Icons/SimpleIcon/interfaces';

export interface InputProps<T> {
  /**
   * Your icon
   * value from TypeSimpleIconClassEnum or string
   */
  icon?: keyof typeof TypeSimpleIconEnum | string;
  /**
   * Side for icon text
   * @default IconSideEnum.left
   */
  iconSide?: keyof typeof IconSideEnum;
  /**
   * Your name for form
   */
  name?: T;
  /**
   * Arrow event
   */
  onPressArrow?: (event: React.KeyboardEvent<HTMLInputElement>) => void;
  /**
   * Enter event
   * */
  onPressEnter?: (event: React.KeyboardEvent<HTMLInputElement>) => void;
  /**
   * Esc event
   * */
  onPressEsc?: (event: React.KeyboardEvent<HTMLInputElement>) => void;
  /**
   * Placeholder value
   */
  placeholder?: string | undefined;
  /**
   * Value
   */
  value: string | number | undefined;
  /**
   * Event for change value and validation result for not withMask props
   * @deprecated
   */
  handleChange?: (
    event: React.ChangeEvent<HTMLInputElement> | any,
    validation?: ValidationOption | undefined,
  ) => void;
  /**
   * Event for return value,  validation, mask
   */
  onChange?: (
    value: string,
    validation?: ValidationOption,
    mask?: any,
  ) => void;
  /**
   * Callback return value, name,  validation, mask
   */
  onChangeField?: (value: string, name: T, validation?: ValidationOption, mask?: any,) => void;
  /**
   * Error message
   * @default false
   */
  hasError?: boolean;
  /**
   * Hint/error message
   */
  hint?: string;
  /**
   * disabled input
   * @default false
   */
  disabled?: boolean;
  /**
   * onClick event
   */
  onClick?: React.FocusEventHandler<HTMLInputElement>;
  /**
   * onBlur event
   */
  onFocus?: React.FocusEventHandler<HTMLInputElement>;
  /**
   * onBlur event
   */
  onBlur?: React.FocusEventHandler<HTMLInputElement>;
  /**
   * add your className
   */
  className?: string;
  /**
   * you label for input
   */
  label?: string;
  /**
   * Validation Options
   * @default [];
   * option @ValidationOption
   */
  validation?: ValidationOption[];
  /**
   * Your mask if withMask on
   * You can get value from event.target.value or event.mask.value/event.mask.unmaskedValue
   * Based on iMask
   * passed any functional from imask
   * docs: https://imask.js.org/
   */
  withMask?: AnyMaskedOptions;
  /**
   * Connect label to input
   * work with label.lenght > 0
   * dont use id if htmlFor is enabled
   * @default false
   */
  htmlFor?: boolean;
}

export enum IconSideEnum {
  left = 'left',
  right = 'right',
}

export enum IconClassEnum {
  left = 'uiKit-input-icon-left',
  right = 'uiKit-input-icon-right',
}

export interface ValidationOption {
  test: RegExp;
  errorMessage: string;
}

export interface OnChangeProps {
  value: string;
  name?: string | undefined;
  validation?: ValidationOption | undefined;
  mask?: any | undefined;
}
