import React, { ChangeEvent, KeyboardEvent, FocusEvent, Ref } from 'react';

export interface InputDynamicProps {
  ref?: Ref<HTMLInputElement>;
  id?: string;
  tag?: keyof typeof InputDynamicTag;
  type?: keyof typeof InputDynamicType;
  className?: string;
  value?: number | string | null;
  hasError?: boolean;
  hasDirty?: boolean;
  checked?: boolean | null;
  disabled?: boolean;
  readOnly?: boolean;
  name?: string;
  autoComplete?: keyof typeof InputAutoCompleteType;
  placeholder?: string;
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
  onInput?: (e: ChangeEvent<HTMLInputElement>) => void;
  onFocus?: (e: FocusEvent<HTMLInputElement>) => void;
  onBlur?: (e: FocusEvent<HTMLInputElement>) => void;
  onPressArrowUp?: (e: KeyboardEvent<HTMLInputElement>) => void;
  onPressArrowDown?: (e: KeyboardEvent<HTMLInputElement>) => void;
  onPressEnter?: (e: KeyboardEvent<HTMLInputElement>) => void;
  onPressEsc?: (e: KeyboardEvent<HTMLInputElement>) => void;
  onClick?: (e: React.MouseEvent<MouseEvent>) => void;
}

export enum InputDynamicTag {
  input = 'input',
  textarea = 'textarea'
}

export enum InputDynamicType {
  checkbox = 'checkbox',
  password = 'password',
  radio = 'radio',
  text = 'text'
}

export enum KeyName {
  ArrowDown = 'ArrowDown',
  ArrowUp = 'ArrowUp',
  Enter = 'Enter',
  Escape = 'Escape'
}

export enum InputAutoCompleteType {
  off = 'off',
  on = 'on',
}
