export interface CardProps {
  /**
   * Handle close callback
   */
  handleClose?: (event: React.MouseEventHandler<HTMLElement>) => void;
  /**
   * Your react children
   */
  children: React.ReactNode;
  /**
   * Size card wrapper
   * @default 'regular'
   */
  size?: keyof typeof TypeCardClassEnum;
  /**
   * Adding your class
   */
  className?: string;
}

export enum TypeCardClassEnum {
  large = 'uiKit-card-large',
  regular = 'uiKit-card-regular',
  small = 'uiKit-card-small',
}

export enum SizeCardEnum {
  large = 'large',
  regular = 'regular',
  small = 'small',
}
