import React, { useMemo, useState, useCallback } from 'react';

import { Typography, TypographyType } from '../../Typography';

import {
  NotificationProps,
  NotificationClassEnum,
  NotificationTypeEnum,
  NotificationSizeEnum,
} from './interfaces';

import './styles.css';

export const Notification = (props: NotificationProps): JSX.Element => {
  const {
    id,
    value,
    size = NotificationSizeEnum.regular,
    type = NotificationTypeEnum.default,
    closable = true,
    className = '',
    closeCallback,
  } = props;

  // useState
  const [isVisible, toggleVisible] = useState<boolean>(true);

  // useMemo
  const memoClassName: string = useMemo(
    () => `${NotificationClassEnum[type]}_${NotificationSizeEnum[size]} ${className}`,
    [type, size, className],
  );
  const TypographyTypeText = useMemo(
    () => (size === NotificationSizeEnum.regular && TypographyType.body)
      || TypographyType.small,
    [size],
  );

  // useCallback
  const handleClose = useCallback(() => {
    toggleVisible(() => {
      if (closeCallback) {
        closeCallback(id);
      }
      return false;
    });
  }, [closeCallback, id]);

  return (
    <>
      {isVisible && (
        <div className={memoClassName}>
          <div className="uiKit-notification-icon" />
          <Typography type={TypographyTypeText} value={value} />
          {closable && (
            <div
              role="button"
              aria-label={value}
              tabIndex={0}
              className="uiKit-notification-close"
              onClick={handleClose}
              onKeyDown={undefined}
            />
          )}
        </div>
      )}
    </>
  );
};
