import React, { useCallback, useState, useEffect } from 'react';

import { Radio } from '../Radio';
import { Typography } from '../../TypographyDeprecated';
import { getRandomNumber } from '../../../utils/getRandomNumber';

import { RadioGroupProps } from './interfaces';

import './styles.css';

export function RadioGroup<T>({
  name,
  label,
  options,
  onChange,
  onChangeField,
  checked,
  className = '',
}: RadioGroupProps<T>) {
  const [selected, changeSelected] = useState(`${checked}`);

  const tryParse = (value: string): string | undefined => {
    try {
      return JSON.parse(value);
    } catch (e) {
      if (value === 'undefined') return undefined;
      return value;
    }
  };

  const handleClick = useCallback(
    (e) => {
      const { target } = e;
      onChange?.(target.name);
      onChangeField?.(tryParse(target.name), name);
      changeSelected(() => `${target.name}`);
    },
    [name, onChange, onChangeField],
  );

  useEffect(() => {
    changeSelected(`${checked}`);
  }, [checked]);

  const customClassName = `uiKit-radioGroup ${className}`;
  return (
    <form className={customClassName}>
      {label && (
        <div className="uiKit-radioGroup-label">
          <Typography type="small" weight="semiBold" value={label} />
        </div>
      )}
      <div className="uiKit-radioGroup-wrapper">
        {options
          && options?.map((option) => {
            const key = getRandomNumber();
            const nameString = `${option.value}`;
            const isChecked = nameString === `${selected}`;
            return (
              <div className="uiKit-radioGroup-item" key={key}>
                <Radio
                  name={nameString}
                  value={option.label}
                  isActive={isChecked}
                  onClick={handleClick}
                />
                <Typography value={option.label} />
              </div>
            );
          })}
      </div>
    </form>
  );
}
