import React, {
  useState, useCallback, useMemo, useEffect, KeyboardEvent, useRef
} from 'react';
import isEqual from 'lodash.isequal';

import { Input } from '../Input';

import { ListItems } from './ListItems';
import { DropdowProps, Option } from './interfaces';
import { findLabel } from './utils';

import './styles.css';

export function Dropdown<T>({
  value,
  onChange,
  onChangeField,
  disabled,
  options,
  name,
  className = '',
  disableFilter,
  ...rest
}: DropdowProps<T>) {
  const selectedLabel = findLabel(value, options);

  const [search, setSearch] = useState(selectedLabel);
  const [isOpenList, changeIsOpenList] = useState(false);
  const [arrowIndex, changeArrowIndex] = useState<number | null>(null);
  const ref = useRef<HTMLInputElement | null>(null);

  const selectedOptions = useMemo(() => {
    if (disableFilter || isEqual(selectedLabel, search)) {
      return options;
    }

    return options.filter((option) => option.label.toLowerCase().includes(search.toLowerCase()));
  }, [disableFilter, options, search, selectedLabel]);

  const handleChange = useCallback((e: string) => {
    if (!disableFilter) {
      setSearch(e);
    }
  }, [disableFilter]);

  useEffect(() => {
    setSearch(findLabel(value, options));
  }, [options, value]);

  // выбор опции через клик
  const handleSelectCallback = useCallback((option: Option) => {
    const { label, value } = option;

    setSearch(label);

    onChange?.(value);
    onChangeField?.(value, name as T);

    changeArrowIndex(null);
  }, [name, onChange, onChangeField]);

  // открытие с клавиатуры options и выбор option через enter
  const handlePressEnter = useCallback((e: React.MouseEvent<HTMLDivElement, MouseEvent> | KeyboardEvent<HTMLInputElement>) => {
    e.persist();

    if (!isOpenList && !disabled) {
      changeIsOpenList(true);
    }

    if (isOpenList && arrowIndex && selectedOptions[arrowIndex]) {
      const { label, value } = selectedOptions[arrowIndex];

      changeIsOpenList(false);
      setSearch(label);

      onChange?.(value);
      onChangeField?.(value, name as T);
      changeArrowIndex(null);

      ref.current?.blur();
    }
  }, [isOpenList, disabled, arrowIndex, selectedOptions, name, onChange, onChangeField]);

  // закрытие меню options
  const handleClose = useCallback(() => {
    if (isOpenList) {
      setSearch(selectedLabel);

      changeIsOpenList(false);
      changeArrowIndex(null);
    }
  }, [isOpenList, selectedLabel]);

  // Навигация по options с помошью стрелок
  const maxLength = selectedOptions?.length - 1 || 0;

  const handlePressArrowDown = useCallback(() => {
    if (isOpenList) {
      changeArrowIndex((current) => {
        if (current === null || current === maxLength || current > maxLength) {
          return 0;
        }

        return current + 1;
      });
    }
  }, [changeArrowIndex, isOpenList, maxLength]);

  const handlePressArrowUp = useCallback(() => {
    if (isOpenList) {
      changeArrowIndex((current) => {
        if (current === null || current === 0 || current > maxLength) {
          return maxLength;
        }

        return current - 1;
      });
    }
  }, [changeArrowIndex, isOpenList, maxLength]);

  return (

    <div
      role="button"
      tabIndex={-1}
      className={`uiKit-select ${className}`}
      onClick={handlePressEnter}
    >
      <Input
        {...rest}
        inputRef={ref}
        name={name as string | undefined}
        disabled={disabled}
        readOnly={disableFilter}
        value={search}
        onChange={handleChange}
        onBlur={handleClose}
        onPressEnter={handlePressEnter}
        onPressEsc={handleClose}
        onPressArrowUp={handlePressArrowUp}
        onPressArrowDown={handlePressArrowDown}
        icon={(isOpenList && 'angleUp') || 'angleDown'}
      />
      {isOpenList && (
        <ListItems
          options={selectedOptions}
          onSelect={handleSelectCallback}
          selectedLabel={selectedLabel}
          arrowIndex={arrowIndex}
        />
      )}
    </div>
  );
}
