import React, { useMemo, useState, useCallback } from 'react';

import { CardProps, SizeCardEnum, TypeCardClassEnum } from './interfaces';

import './styles.css';

export const Card = (props: CardProps): JSX.Element => {
  const {
    children, size = SizeCardEnum.regular, className = '', handleClose
  } = props;

  // useState
  const [isVisible, toggleVisible] = useState<boolean>(true);

  // useMemo
  const memoClassName: string = useMemo(
    () => `${TypeCardClassEnum[size]} ${className}`,
    [size, className],
  );

  // useCallback
  const handleClick = useCallback(
    (event) => {
      toggleVisible(() => {
        if (handleClose) handleClose(event);
        return false;
      });
    },
    [handleClose, toggleVisible],
  );

  return (
    <>
      {isVisible && (
        <div className={memoClassName}>
          <div className="uiKit-card-header">
            <button type="button" aria-label=" " onClick={handleClick} />
          </div>
          <div className="uiKit-card-content">{children}</div>
        </div>
      )}
    </>
  );
};
