export interface AutoCloseNotificationProps {
  /**
   * Value
   * */
  value: string;
  /**
   * Close timer in ms
   * @default 5000
   */
  timeToClose?: number;
  /**
   * Adding your class
   * */
  className?: string;
  /**
   * Type notification
   * @default 'warning'
   * */
  type?: keyof typeof AutoCloseNitificationType;
  /**
   * ID for targeting close
   */
  id: number;
  /**
   * closeCallback
   */
  closeCallback?: (id: number) => void;
}

export enum AutoCloseProgressClassEnum {
  danger = 'uiKit-autoCloseNotification-progress_danger',
  success = 'uiKit-autoCloseNotification-progress_success',
  warning = 'uiKit-autoCloseNotification-progress_warnign',
}

export enum AutoCloseNitificationType {
  danger = 'danger',
  success = 'success',
  warning = 'warning',
}
