export interface CheckboxProps<T> {
  /**
   * Action for toggle checkbox
   */
  onChangeField?: (val: boolean, name: T) => void;
  /**
   * selected checkbox
   */
  name?: T;
  /**
   * selected checkbox
   * @default false
   */
  isActive: boolean;
  /**
   * Error message
   * @default false
   */
  hasError?: boolean;
  /**
   * disabled input
   * @default false
   */
  disabled?: boolean;
  /**
   * disabled input
   * @default TypeCheckboxEnum.normal
   */
  type?: keyof typeof TypeCheckboxEnumClassEnum;
  /**
   * Action for toggle checkbox
   * @deprecated
   */
  onClick?: (event: React.ChangeEvent<HTMLInputElement>) => void;
  /**
   * Action for toggle checkbox
   */
  onChange?: (val: boolean) => void;

  /**
   * Action for toggle checkbox
   */
  className?: string;
}

export enum TypeCheckboxEnum {
  indeterminate = 'indeterminate',
  normal = 'normal',
}

export enum TypeCheckboxEnumClassEnum {
  indeterminate = 'uiKit-button-indeterminate',
  normal = 'uiKit-checkbox-normal',
}
